const valorAnterior = document.getElementById("valor-anterior");
const valorActual = document.getElementById("valor-actual");
const botones = document.querySelectorAll("#botones button");
// console.log(botones);

let operationComplete = false;

function lastValue(){
  return  valorAnterior.textContent.substring(valorAnterior.textContent.length-1) ;
}

function deleteText(){
  valorAnterior.textContent = valorAnterior.textContent.substring(0,valorAnterior.textContent.length-1);

  if (valorAnterior.textContent== ' ') valorAnterior.textContent = '0';
}

function writeOperation(text) {
   if (valorAnterior.textContent==0) valorAnterior.textContent = '';

    if(operationComplete && isNaN(text)){
      valorAnterior.textContent = valorActual.textContent;
      operationComplete = false;
    }

    if(operationComplete  && !isNaN(text)){
      valorAnterior.textContent = " ";
      valorActual.textContent = " ";
      operationComplete = false;
    }

    if(isNaN(lastValue()) && isNaN(text) ){
     valorAnterior.textContent =  valorAnterior.textContent.substring(0,valorAnterior.textContent.length-1)
    }
      valorAnterior.textContent += text;
    
    
}



function writeResult() {
  valorActual.textContent = eval(valorAnterior.textContent);

  operationComplete = true;
}

const resetScreen = () =>{
  valorAnterior.textContent = '0';
  valorActual.textContent = '0';
}

botones.forEach((boton) => {
  boton.addEventListener("click", (e) => {
    const valor = e.target.textContent;
    if (valor !== ' ') {
      switch (valor) {
        case "=":  writeResult(); break;
        case "Limpiar": resetScreen();break;
        case ",": writeOperation(","); break;
        case "DEL": deleteText(); break;
        default: writeOperation(e.target.textContent);break;
      }
    }
  })
});


//Temas 

const temas = document.querySelectorAll('#tema button');
const body = document.querySelector('body');
const togle = document.querySelector('#toggle');

temas.forEach(tema =>{
  tema.addEventListener('click', e =>{
    const valor = e.target.dataset.tema;
   
    if(valor == 1){
      body.classList.add('tema1');
    }else {
      body.classList.remove('tema1');
    } 

    if(valor == 2){
      body.classList.add('tema2');
    }else {
      body.classList.remove('tema2');
    } 

    if(valor == 3){
      body.classList.add('tema3');
    }else {
      body.classList.remove('tema3');
    } 
  })
})